/* LESS Document */

body {
	background: #fff;
	color: @brand-gray;
	font-family: 'mr-eaves-xl-modern', sans-serif;
	font-size: 18px;
	font-weight: 200;
	height: 100%;
	margin: 0 auto;
	width: 100%;
}

#preheader {
	position: absolute;
	width: 100%;
	z-index: 50;
	
	.preheader-inner {
		margin-left: auto;
		margin-right: auto;
		max-width: 2000px;
		padding: 10px 20px;
	}
	
	
	#logo {
		margin-top: 35px;
		text-align: center;
		
		img, h5 {
			display: inline-block;
			vertical-align: middle;
		}
		
		img {
			margin-right: 12px;
			max-width: 60px;
			
			@media (min-width: 768px) {
				max-width: 100%;	
			}
		}
		
		span {
			color: @white;
			font-size: 28px;
			font-weight: @medium;
			letter-spacing: 3px;
			text-transform: lowercase;
			vertical-align: middle;
			text-align: center;
			
			@media (min-width: 768px) {
				font-weight: @light;
				letter-spacing: 4px;
			}
		}
		
		@media (min-width: 768px) {
			float: left;
			margin-top: 0;
			text-align: left;
			
			span {
				font-size: 36px;
			}
		}
	}
	
	#nav {
		background: @white;
		box-shadow: inset -1px -1px 2px 0px rgba(103, 103, 103, 0.3);
		-webkit-box-shadow: inset -1px -1px 2px 0px rgba(103, 103, 103, 0.3);
		-moz-box-shadow: inset -1px -1px 2px 0px rgba(103, 103, 103, 0.3);
		-o-box-shadow: inset -1px -1px 2px 0px rgba(103, 103, 103, 0.3);
		left: 0;
		position: absolute;
		right: 0;
		top: 0;
		width: 100%;
		
		#nav-icon {
			padding: 9px 16px;
			text-align: right;
			
			.bar {
				background: @brand-primary;
				display: block;
				height: 3px;
				margin: 0 0 4px auto; 
				width: 30px;
				
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
				
		ul {
			display: none;
			
			@media (min-width: 768px) {
				display: block;
			}
			
			li {
				@media (min-width: 768px) {
					display: inline-block;
					margin: 0 40px;
					
					&:first-child {
						margin-left: 0;
					}
					
					&:last-child {
						margin-right: 0;
					}
					
					a {
						color: @white;
						transition: color 0.2s ease-in-out;
						-moz-transition: color 0.2s ease-in-out;
						-o-transition: color 0.2s ease-in-out;
						-webkit-transition: color 0.2s ease-in-out;
						
						&:hover {
							color: @brand-gray;
						}
					}
				}
			}
			
			.active {
				a {
					color: @brand-gray;
					font-weight: @bold;
				}
			}
		}
		
		@media (min-width: 768px) {
			background: transparent;
			box-shadow: none;
			float: right;
			margin-top: 25px;
			position: relative;
			width: auto;
			
			#nav-icon {
				display: none;
			}
		}
	}
}

#footer {
	padding: 25px 15px 5px 15px;
	text-align: center;
	
	
	.social {
		margin: 30px 0 @md-pad 0;
		
		li {
			display: inline-block;
			margin: 0 15px;
		}
	}
	
	.about-site {
		color: @brand-primary;
		display: block;
		margin-bottom: 5px;
	}
	
	p {
		font-size: 16px;
	}
}