/* Container
---------------------------------------------------------------------- */

.container {
    line-height: 0;
	margin-top: 45px;
    text-align: justify;
	
	@media (min-width: 700px) and (max-width: 992px) {
		margin-left: auto;
		margin-right: auto;
		max-width: 760px;
	}
}

.container:after {
    content: '';
    display: inline-block;
    width: 100%;
}

/* Target Elements
---------------------------------------------------------------------- */

.mix {
	margin-bottom: 40px;
	position: relative;
	
	@media (min-width: 992px) {
		margin-bottom: 15px;	
	}
	
	.mix-inner {
		margin: 0 auto;
		max-width: 316px;
		position: relative;
		width: 100%;
	}
	
	img {
		border: 1px solid #ddd;
	}
	
	.info {
		margin-top: 10px;
		text-align: center;
		
		.info-inner {
			line-height: 1.15;
			
			h3 {
				margin-bottom: 5px;
			}
		}
		
		@media (min-width: 992px) {
			background: rgba(80, 80, 80, .75);
			height: 100%;
			margin-top: 0;
			opacity: 0;
			position: absolute;
			top: 0;
			transition: opacity .4s ease-in-out;
			width: 100%;

			.info-inner {
				left: 0;
				padding: 0 10px;
				position: absolute;
				right: 0;
				top: 25%;

				h3 {
					color: @white;
					font-size: 22px;
					margin-bottom: 0;
				}

				.btn {
					margin-top: 10px;
					
					.btn-primary();
				}
			}
		}
	}

	> a {
		display: block;
	}

	&:hover {
		.info {
			opacity: 1;
		}
	}
}
		


