#hero.secondary-header {
	height: auto;
	min-height: auto;
	padding: 57px 0;
}

.work-sample {
	margin-top:  @sm-pad;
	padding: 0 10px 0 10px;
	
	@media (min-width: 500px) {
		padding-left: 15px;
		padding-right: 15px;
	}
	
	.inner {
		text-align: center;
	}

	h3 {
		margin-bottom: 10px;
	}
	
	p {
		margin-bottom: @xs-pad;
	}
	
	img {
		margin-bottom: @sm-pad;
	}
	
	.work-img {
		border: 1px solid #ddd;
		box-shadow: 2px 2px 2px 0px rgba(187, 187, 187, 0.2);
		-webkit-box-shadow: 2px 2px 2px 0px rgba(187, 187, 187, 0.2);
		-moz-box-shadow: 2px 2px 2px 0px rgba(187, 187, 187, 0.2);
		padding: 10px;
	}
	
	.inline {
		margin: 15px 0;
		
		@media (min-width: 800px) {
			display: inline-block;
			margin: 0 15px;
		}
	}
}

.work-back {
	.gradient();
	margin-bottom: 15px;
	
	.go-back {
		color: @white;
		display: block;
		padding: 10px;
		text-align: left;
		transition: color 0.2s ease-in-out;
		-moz-transition: color 0.2s ease-in-out;
		-o-transition: color 0.2s ease-in-out;
		-webkit-transition: color 0.2s ease-in-out;

		&:hover {
			color: @brand-gray;
	 	}
	}
}

#cards-page {
	.work-img {
		padding: 0;
	}
}



