/* LESS Document */

#hero {
	background: @brand-primary; /* Old browsers */
	background-image: url(../assets/pattern-background.png), -moz-linear-gradient(135deg, @brand-secondary 0%, @brand-primary 100%); /* FF3.6-15 */
	background-image:  url(../assets/pattern-background.png), -webkit-linear-gradient(135deg, @brand-secondary 0%, @brand-primary 100%); /* Chrome10-25,Safari5.1-6 */
	background-image:  url(../assets/pattern-background.png), linear-gradient(135deg, @brand-secondary 0%, @brand-primary 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@brand-secondary', endColorstr='@brand-primary',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
	height: 90vh;
	min-height: 85%;
	padding: 100px 0;
	position: relative;
	
	@media (min-width: 768px) {
		background-attachment: fixed;
	}
	
	#intro {
		left: 0;
		position: absolute;
		right: 0;
		text-align: center;
		top: 30%;
		
		h1 {
			color: @white;
			font-size: 45px;
			line-height: 55px;
			text-shadow: 1.5px 1.5px 1.5px rgba(88, 85, 85, 0.24);
			
			@media (min-width: 768px) {
				font-size: 76.244px;
				line-height: 55px;
				
				br {
					display: none;
				}
			}
			
			span {
				font-size: 60px;
				letter-spacing: 0;
				text-transform: lowercase;
				.script();
				
				@media (min-width: 768px) {
					font-size: 85px;
				}
			}
		}
		
		h3 {
			margin-top: 10px;
		}
	}
	
	#hero-cta {
		margin-top: 40px;
		
		.btn {
			margin: 0 10px;
			
			a {
				padding: 15px 25px;
			}
						
			@media (max-width: 767px) {
				margin: 0 5px;
				
				a {
					font-size: 16px;
					padding: 10px 15px;
				}
			}
		}
	}
}

#hero.temporary {
	height: 100vh;
	min-height: 100%;
	
	#intro {
		top: 40%;
	}
}

#about-intro {
	.home-section-alt();
	
	img {
		margin-bottom: 20px;
	}
	
	span {
		display: block;
		font-size: 21.391px;
		text-transform: none;
		.script();
	
		@media(min-width: 768px) {
			font-size: 29.124px;
		}
	}
	
	
	h3 {
		background: -webkit-linear-gradient(135deg, @brand-primary 0%, @brand-secondary 100%);
		color: @brand-primary;
		display: inline-block;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}
}

#history {
	.gradient();
	.home-section();
	
	h2 {
		color: @white;
		margin-bottom: 60px;
	}
	
	#timeline {
		background: url(../assets/timeline-spine.png) no-repeat 22% top;
		margin: 0 auto;
		max-width: 100%;
		width: 340px;
		
		.event {			
			.clearfix();
			
			.event-date {
				display: inline-block;
				float: left;
				text-align: right;
				width: 20%;
				
				h3 {
					color: @white;
				}
			}
			
			.event-text {
				background: url(../assets/event-pointer-right.png) no-repeat left 30%;
				float: right;
				margin-top: -10px;
				padding-left: 13px;
				width: 69%;
				
				.text-inner {
					background: @white;
					border-radius: .6em;
					box-shadow: 2px 2px 2px 0px rgba(69,52,52,.2);
					-webkit-box-shadow: 2px 2px 2px 0px rgba(69,52,52,.2);
					-moz-box-shadow:2px 2px 2px 0px rgba(69,52,52,.2);
					display: inline-block;
					letter-spacing: .4px;
					font-size: 18px;
					font-weight: 200;
					padding: 15px 12px;
					text-align: left;
					line-height: 20px;
					
					span {
						color: @brand-primary;
						font-size: 26px;
						.script();
					}
				}
			}
		}
				
		#event-two {
			margin-top: 176px;
			
			.event-text {
				margin-top: -17px;				
			}
		}

		#event-three {
			margin-top: 32px;
		}

		#event-four {
			margin-top: 55px;
			
			.event-text {
				margin-top: -10px;
			}
		}

		@media (min-width: 768px) {
			background-position: top center;
			width: 600px;
			
			.event {
				.event-date {
					width: 46%;
				}
				
				.event-text {
					width: 46%;
				}
			}
			
			.event-alt {
				.event-date {
					float: right;
					text-align: left;
				}

				.event-text {
					background: url(../assets/event-pointer.png) no-repeat right 30%;
					float: left;
					padding-left: 0;
					padding-right: 14px;
					text-align: right;

					.text-inner {
						box-shadow: -1px 2px 2px 0px rgba(69,52,52,.2);
						-webkit-box-shadow: -1px 2px 2px 0px rgba(69,52,52,.2);
						-moz-box-shadow: -1px 2px 2px 0px rgba(69,52,52,.2);
						text-align: right;
					}
				}
			}
			
			#event-two {
				.event-text {
					margin-top: -10px;
				}	
			}
			
			#event-three {
				margin-top: 45px;	
			}
			
			#event-four {
				.event-text {
					margin-top: -10px;
				}	
			}
		}

		@media (min-width: 992px) {
			width: 800px;

			.event {
				.event-date {
					width: 47%;
				}

				.event-text {
					margin-top: -13px;
					width: 47%;

					.text-inner {
						font-size: 20px;
						padding: 20px 30px;

						span {
							font-size: 21.391px;
	
							@media(min-width: 768px) {
								font-size: 29.124px;
							}
						}
					}
				}
			}
			
			#event-two {
				margin-top: 169px;
				
				.event-text {
					margin-top: -12px;
				}
			}
			
			#event-three {
				margin-top: 38px;
				
				.event-text {
					margin-top: -14px;
				}
			}
			
			#event-four {
				margin-top: 50px;
				
				.event-text {
					margin-top: -12px;
				}
			}
		}
	}	
}

	
#mycons {
	.home-section();
	.maxwidth();
	
	#featured-icons {
		.clearfix();
		
		.ft-icon {
			border: 1px solid #dfe2e3;
			box-shadow: 2px 2px 2px 0px rgba(187, 187, 187, 0.2);
			-webkit-box-shadow: 2px 2px 2px 0px rgba(187, 187, 187, 0.2);
			-moz-box-shadow: 2px 2px 2px 0px rgba(187, 187, 187, 0.2);
			margin: 20px 0;
			padding: 20px 15px;
			
			.icon-img, p {
				display: inline-block;
				vertical-align: middle;	
			}
			
			.icon-img {
				margin-right: 3%;
				max-width: 80px;
				width: 20%;
			}
			
			p {
				width: 75%;
			}
			
			@media (min-width: 500px) {
				.icon-img {
					margin-right: 5%;
				}
			}
			
			@media (min-width: 768px) {
				float: left;
				margin: 10px;
				padding: 20px 25px;
				width: 47%;
				
				.icon-img {
					margin-right: 25px;
					width: 20%;
				}
				
				p {
					width: 70%;
				}
			}
			
			@media (min-width: 992px) {
				.icon-img {
					margin-right: 35px;
					width: 25%;
				}
			}
		}
	}
	
	#icon-grid {
		margin-top: @md-pad;
		.clearfix();
		
		.icon {
			float: left;
			margin: 0 0 @sm-pad 0;
			padding: 0 15px;
			text-align: center;
			width: 50%;
			
			img {
				margin-bottom: 5px;
			}
			
			@media (min-width: 992px) {
				width: 25%;
			}
		}
	}
}

#resume {
	.gradient();
	.home-section-alt();
	
	.btn {
		margin: 15px 0;
		
		 a {
			font-size: 21.391px;
			padding: .5em 2em;
			 
			@media(min-width: 500px) {
				padding: .5em 3em;
			}
			 
			@media(min-width: 768px) {
				font-size: 29.124px;
			}			
		}
	}
}

#work {
	.home-section();
	.maxwidth();
	
	h2 {
		margin-bottom: @xs-pad;
	}
	
	#what-i-do {
		margin-bottom: @md-pad;
	}
}

#contact {
	color: @white;
	.gradient();
	.home-section();
	
	h2 {
		margin-bottom: 10px;
	}
	
	p {
		text-align: center;
		
		span {
			background: url(../assets/available-light.png) no-repeat -8% center;
			font-weight: 400;
			padding: 6px 0 7px 22px;
		}
	}
	
	iframe {
		border: none;
		height: 400px;
		width: 100%;
	}
}

#form-page {
	background: transparent;
	overflow: hidden;
	
	#contact-form {
		margin: @sm-pad auto 0 auto;
		max-width: 750px;

		form {			
			input, textarea {
				border: none;
				border-radius: .6em;
				box-shadow: inset 2px 2px 2px 0px rgba(69, 52, 52, 0.2);
				-webkit-box-shadow: inset 2px 2px 2px 0px rgba(69, 52, 52, 0.2);
				-moz-box-shadow: inset 2px 2px 2px 0px rgba(69, 52, 52, 0.2);
				font-family: 'mr-eaves-xl-modern';
				font-weight: 200;
				letter-spacing: 1px;
				padding: 10px;

				&:focus {
					outline: none;
				}
			}

			.top-row {
				.clearfix();
			}

			#name-input, #email-input {
				float: left;
				width: 46.5%;

				input {
					width: 100%;
				}
			}

			#name-input {
				margin-right: 7%;
			}

			#message-input {
				margin-top: 20px;

				textarea {
					height: 140px;
					width: 100%;
				}
			}

			#submit-btn {
				margin-top: 25px;

				input {
					background: @brand-gray;
					border-radius: 2em;
					color: @white;
					font-weight: 400;
					font-size: 18px;
					padding: .5em 1em;
					.btn-shadow();
					transition: color 0.2s ease-in-out;
					-moz-transition: color 0.2s ease-in-out;
					-o-transition: color 0.2s ease-in-out;
					-webkit-transition: color 0.2s ease-in-out;
					
					&:hover {
						color: @brand-secondary;
						cursor: pointer;
					}
				}
			}
		}
	}
	
	p {
		background: url('../assets/email-error-icon.png') left 45% no-repeat;
		font-weight: bold;
		margin: 30px auto 0 auto;
		max-width: 750px;
		padding-left: 15px;
	}
	
	#submit-msg {
		background: url('../assets/email-success-icon.png') left 45% no-repeat;
	}
}



